body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    background: url(/static/media/galaxies.f4745f64.JPG);
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    width: 1800px;
    height: 1200px;
    background-size: auto;
    overflow: auto;
    top: 0;
    bottom: 0;
    position: relative;
    left: 0;
    right: 0;
    justify-content: center;
}

html, body {
    height: 100%;
    overscroll-behavior: none;
}



#text {
    margin-right: 12%;
    margin-left: 6%;
    display: inline-block;
    position: absolute;
}

h3 {
    font-size: 2.00em;
    font-family: Tahoma;
    background-color: slategrey;
    color: black;
    padding: 3%;
    border-radius: 5%;
    text-align: center;
}
h2 {
    font-size: 2.00em;
    font-family: Tahoma;
    background-color: coral;
    color: black;
    padding: 3%;
    border-radius: 5%;
    text-align: center;
}

.MilesContainer {
    padding-right: 15%;
    padding-top: 25%;
    display: inline-block;
    position: absolute;
    margin-left: 20%;
    margin-right: 20%;
}
